<template>
  <v-navigation-drawer
    v-model="localOpen"
    v-if="!$vuetify.breakpoint.smAndUp"
    app
    clipped
    class="elevation-1"
    :mini-variant="mini"
    :expand-on-hover="mini"
    temporary
  >
    <v-list>
      <v-list-group
        v-model="item.active"
        :append-icon="item.type === 'link' ? '': '$expand'"
        v-for="(item, i) in menuItems"
        :key="i"
        prepend-icon="mdi-restaurant"
        no-action
        color="dark"
      >
        <template v-slot:activator>
          <v-list-item-content>
            <v-list-item-title v-text="item.title"></v-list-item-title>
          </v-list-item-content>
        </template>

        <v-list-item
          v-for="(child, j) in item.children"
          :key="`child-${j}`"
          @click="d"
        >
          <v-list-item-content>
            <v-list-item-title>
              <router-link :to="child.to" class="text--secondary item-link">
                {{ child.title }}
              </router-link>
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list-group>
    </v-list>
    <v-divider />
    <v-list>
      <v-list-item
        v-for="(item, i) in linkItems"
        :key="i"
      >

        <v-list-item-content>
          <v-list-item-title>
            <router-link :to="item.to" class="text--secondary item-link">
              {{ item.title }}
            </router-link>
          </v-list-item-title>
        </v-list-item-content>
      </v-list-item>
    </v-list>
  </v-navigation-drawer>
</template>

<script>
export default {
  name: 'Drawer',
  components: {},
  props: {
    items: Array,
    open: Boolean,
    mini: Boolean,
  },
  data: () => ({
    localOpen: false,
  }),
  computed: {
    menuItems() {
      return this.items.filter(item => item.type === "menu");
    },
    linkItems() {
      return this.items.filter(item => item.type === "link");
    }
  },
  watch: {
    open: function(){
      this.localOpen = this.open
    },
    localOpen: function() {
      if (this.open !== this.localOpen) {
        this.$emit("toggle-drawer")
      }
    }
  },
  methods: {
    d(){},
  }
}
</script>

<style scoped>
.item-link {
  text-decoration: none;
}
</style>
