<template>
  <v-app-bar 
    app
    dense
    clipped-left
    color="primary elevation-1"
    :height="styles.height ? styles.height : 48"
  >
    <v-app-bar-nav-icon v-if="!$vuetify.breakpoint.smAndUp" @click="$emit('drawer-click')" />
    <v-col v-if="$vuetify.breakpoint.smAndUp" cols="3" />
    <v-toolbar-title>
      <router-link to="/website" class="text--secondary item-link">
        {{ title }}
      </router-link>
    </v-toolbar-title>
    <v-col cols="1" />
    <template v-if="$vuetify.breakpoint.smAndUp">
      <v-col 
        v-for="(item, i) in items" 
        :key="i"
        cols="1"
      >
        <router-link :to="item.to">
          <v-menu class="elevation-0" open-on-hover offset-y>
            <template v-slot:activator="{ on }">
              <v-row
                class="text--secondary"
                justify="center"
                v-on="on"
                @mouseover="hoverTitle(item.title)"
              >
                {{ item.title }}
              </v-row>
            </template>
            <v-list
              v-if="dropdown && item.children.length > 0"
              dense
              elevation="0"
            >
              <v-list-item
                v-for="(child, index) in item.children"
                :key="`child-${index}`"
              >
                <v-list-item-title>
                  <router-link :to="child.to || ''">
                    {{ child.title }}
                  </router-link>
                </v-list-item-title>
              </v-list-item>
            </v-list>
          </v-menu>
        </router-link>
      </v-col>
      <v-spacer />
      <v-col v-if="false" cols="1">
        <router-link to="/website/login" class="text--secondary item-link">
          登入
        </router-link>
      </v-col>
    </template>
    <template v-if="!dropdown && currentChildren.length > 0" v-slot:extension>
      <v-row justify="center" class="secondary" @mouseleave="hoverTitle('none')">
        <v-spacer />
        <v-col
          v-for="(child, i) in currentChildren"
          cols="1"
          :key="i"
          
        >
          <v-btn text :ripple="false" :to="child.to">
            {{ child.title }}
          </v-btn>
        </v-col>
        <v-spacer />
      </v-row>
    </template>
  </v-app-bar>
</template>

<script>
export default {
  name: 'AppBar',
  props: {
    title: String,
    styles: Object,
    items: Array,
    dropdown: Boolean,
  },
  components: {},
  data: () => ({
    currentHoverTitle: "",
  }),
  computed: {
    currentChildren() {
      let currentItem = this.items.find((item) => item.title === this.currentHoverTitle)
      return currentItem ? currentItem.children : [];
    },
  },
  methods: {
    hoverTitle(title) {
      this.currentHoverTitle = title
    },
  }
}
</script>

<style>
.v-toolbar__extension {
  padding-left: 0px !important;
  padding-right: 0px !important;
}
</style>
<style scoped>
.item-link {
  text-decoration: none;
}
</style>
